@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@300&display=swap');

* {
  box-sizing:border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 2rem;
  text-align: center;
}

h1 {
  font-size: 10rem;
  text-align: center;
  margin: 0;
}

.app{
  width: 100%;
  min-height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.5);
  color: white;

}
.app .search {
  text-align: center;
  padding: 1rem;
}

.app input {
  text-align: center;
  padding: .7rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px rgba(255, 255, 255, 0.8);
  background: rgba(255, 255, 255, 0.1);
  color: #f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}

.app:before {
  content: '';
  background: url('./assets/atmosphere-768x378.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container {
  max-width: 700px;
  height: 700px;
  margin: auto;
  padding: 0 2rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
  transform-origin: 0 0;
}

.app .bottom {
  display: flex;
  font-size: 2rem;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  margin-bottom: 12rem;
  padding: 1rem;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.2);
}

.bold {
  font-weight: 1000;
}